@media (min-width: 576px) {
  .story-text__card {
    padding: 6rem 10rem !important; } }

.story-text__card__copy {
  font-size: 2.2rem;
  border-left: 1px solid #c50000;
  padding: 0 0 0 2rem; }
  @media (min-width: 575px) {
    .story-text__card__copy {
      padding: 0 0 0 4rem; } }
  @media (max-width: 575px) {
    .story-text__card__copy {
      font-size: 30px; } }

.story-text__card h5 {
  text-transform: capitalize;
  padding: 0 0 0 2rem;
  margin-left: 0rem; }
  @media (min-width: 575px) {
    .story-text__card h5 {
      padding: 0 0 0 4rem; } }

.story-text__card__author {
  letter-spacing: 6px;
  font-size: 10px;
  color: #c50000;
  text-transform: uppercase;
  margin-left: 2rem; }
  @media (min-width: 575px) {
    .story-text__card__author {
      margin-left: 4rem; } }

.loading-animation {
  width: 100%;
  height: 100%;
  padding: 6rem 0;
  margin: 0 auto;
  text-align: center; }
  .loading-animation__icon img {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    animation-name: loading-spinner;
    animation-duration: 1.3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out; }

@keyframes loading-spinner {
  from {
    transform: rotateZ(0deg); }
  to {
    transform: rotateZ(360deg); } }
